@import "/src/styles/fonts/google_fonts.css";

*, :before, :after {
  box-sizing: border-box;
}

ul[class], ol[class] {
  padding: 0;
}

body, h1, h2, h3, h4, h5, h6, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

ul, ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input, button, textarea, select {
  font: inherit;
}

.swiper {
  box-sizing: content-box;
  height: auto;
  margin: 0 auto;
  display: flex;
}

.swiper-slide {
  display: flex;
}

.swiper-slide img {
  width: 100%;
  margin: 0 auto;
}

.swiper-button-prev {
  left: 20px;
}

.swiper-button-prev:after {
  color: #111;
  font-size: 60px;
}

@media screen and (width <= 900px) {
  .swiper-button-prev:after {
    font-size: 40px;
  }
}

@media screen and (width <= 400px) {
  .swiper-button-prev:after {
    font-size: 25px;
  }
}

.swiper-button-next {
  right: 20px;
}

.swiper-button-next:after {
  color: #111;
  font-size: 60px;
}

@media screen and (width <= 900px) {
  .swiper-button-next:after {
    font-size: 40px;
  }
}

@media screen and (width <= 400px) {
  .swiper-button-next:after {
