.patch {
  max-width: 100%;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas: "e a f f"
                       "b d d c";
  margin: 0 auto;
  display: grid;
}

.patch :first-child {
  grid-area: a;
}

.patch :nth-child(2) {
  grid-area: b;
}

.patch :nth-child(3) {
  grid-area: c;
}

.patch :nth-child(4) {
  grid-area: d;
}

.patch :nth-child(5) {
  grid-area: e;
}

.patch :nth-child(6) {
  text-align: center;
  background-color: #e7828282;
  grid-area: f;
}

.patch_img p {
  padding-top: 15px;
  font-family: Lobster, cursive;
  font-size: 58px;
}

@media screen and (width <= 1189px) {
  .patch p {
    padding-top: 10px;
    font-size: 55px;
  }
}

@media screen and (width <= 1085px) {
  .patch p {
    padding-top: 10px;
    font-size: 50px;
  }
}

@media screen and (width <= 1024px) {
  .patch {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "f f f"
                         "e a b"
                         "d d c";
    grid-row: 0px;
  }
}

@media screen and (width <= 980px) {
  .patch p {
    padding-top: 15px;
    font-size: 60px;
  }
}

@media screen and (width <= 900px) {
  .patch p {
    padding-top: 10px;
    font-size: 55px;
  }
}

@media screen and (width <= 811px) {
